import { useEffect } from 'react';
import aos from 'aos';

import Page from '../../components/Page';
import Hero from "./sections/Hero";
import Features from "./sections/Features";
import Currencies from "./sections/Currencies";
import Banners from "./sections/Banners";
import useParsedQueryString from "../../hooks/useParsedQueryString";
import {toast} from "react-hot-toast";
import {useActiveWeb3React} from "../../hooks";

const HomePage = props => {
    const { account } = useActiveWeb3React();
    const queryString = useParsedQueryString();

    useEffect(() => {
        aos.init();
    }, [])

    useEffect(() => {
        if(queryString && queryString.error) {
            if(account) {
                toast.error('The page you\'re looking for is not found.');
            } else {
                toast.error('Please connect to your wallet first');
            }
        }
    }, [queryString])

    return (
        <Page disableAccountCheck={true}>
            {/* Home-Hero: start */}
            <Hero/>
            {/* Home-Hero: end */}


            {/* Home-Currencies: start */}
            <Banners />
            {/* Home-Currencies: end */}

            {/* Home-Currencies: start */}
            <Currencies/>
            {/* Home-Currencies: end */}

            {/* Home-Features: start */}
            <Features/>
            {/* Home-Features: end */}
        </Page>
    )
}

export default HomePage;